
'use client';

import { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
// Firebase imports removed
// import { doc, getDoc, Timestamp } from 'firebase/firestore';
// import { db } from '@/config/firebase';
import type { Case, DocumentUpload, JudgeNote } from '@/types';
import { mockCases } from '@/data/mockData'; // Use mock data
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import {
  ArrowLeft, CalendarDays, FileText, Gavel, Landmark, Scale, User, Users, CheckCircle2, AlertCircle,
  Download, MessageSquare, Clock, Activity, PauseCircle, ScrollText, AlertTriangleIcon, Edit, Trash2, UploadCloud, Search, LayoutDashboard
} from 'lucide-react';
import Link from 'next/link';
import Image from 'next/image'; // Keep if used, otherwise remove
import { Skeleton } from '@/components/ui/skeleton';
import { useAuth } from '@/contexts/AuthContext';
import { useToast } from '@/hooks/use-toast';
import { useTranslation } from 'react-i18next';
import { isValid, parseISO, format } from 'date-fns';
import { enUS, es, fr, hi } from 'date-fns/locale'; // Keep locales for date formatting

const statusIcons: Record<Case['status'], React.ElementType> = {
  Pending: Clock,
  Filed: FileText,
  Investigation: Search,
  'In Progress': Activity,
  Hearing: Gavel,
  Judgement: ScrollText,
  Resolved: CheckCircle2,
  Appealed: AlertCircle,
  'On Hold': PauseCircle,
  Closed: CheckCircle2,
};

const statusColors: Record<Case['status'], string> = {
  Pending: 'bg-gray-500/20 text-gray-700 border-gray-500/30',
  Filed: 'bg-blue-500/20 text-blue-700 border-blue-500/30',
  Investigation: 'bg-yellow-500/20 text-yellow-700 border-yellow-500/30',
  'In Progress': 'bg-cyan-500/20 text-cyan-700 border-cyan-500/30',
  Hearing: 'bg-purple-500/20 text-purple-700 border-purple-500/30',
  Judgement: 'bg-indigo-500/20 text-indigo-700 border-indigo-500/30',
  Resolved: 'bg-green-500/20 text-green-700 border-green-500/30',
  Appealed: 'bg-orange-500/20 text-orange-700 border-orange-500/30',
  'On Hold': 'bg-pink-500/20 text-pink-700 border-pink-500/30',
  Closed: 'bg-green-700/30 text-green-800 border-green-700/40',
};

const dateLocales: { [key: string]: Locale } = {
  en: enUS, es: es, fr: fr, hi: hi,
};

export default function CaseDetailPage() {
  const router = useRouter();
  const params = useParams();
  const caseId = params.id as string;
  const [caseData, setCaseData] = useState<Case | null>(null);
  const [loading, setLoading] = useState(true);
  const { user, loading: authLoading } = useAuth();
  const { toast } = useToast();
  const { t, i18n } = useTranslation();

  const getCurrentLocale = () => {
    return dateLocales[i18n.language] || enUS;
  };

 // Modified formatDate to handle string dates from mock data
  const formatDate = (dateInput: string | undefined | null, includeTime = false) => {
      if (!dateInput) return t('na');
      let date: Date | null = null;

      try {
        if (typeof dateInput === 'string') {
          date = parseISO(dateInput); // Assuming ISO string format from mock data
        } else if (dateInput instanceof Date) {
           date = dateInput;
        }


        if (date && isValid(date)) {
          const options: Intl.DateTimeFormatOptions = { year: 'numeric', month: 'long', day: 'numeric' };
          if (includeTime) {
            options.hour = '2-digit';
            options.minute = '2-digit';
          }
          // Use i18n language for formatting
          return date.toLocaleDateString(i18n.language, options);
        }
      } catch (e) {
        console.error("Error formatting date:", e, "Input:", dateInput);
      }
      return t('na');
    };


  useEffect(() => {
    const fetchCase = () => {
      if (!caseId || !user) {
        setLoading(false);
        return;
      }
      setLoading(true);

      // Find case from mock data
      const foundCase = mockCases.find(c => c.id === caseId);

      if (foundCase) {
           // Basic client-side check for role-based access (using mock data)
           const isJudge = user.role === 'Judge' && user.id === foundCase.judgeId;
           const isOfficial = user.role === 'CourtOfficial';
           const isPlaintiff = user.id === foundCase.plaintiffId;
           const isDefendant = user.id === foundCase.defendantId;
           const isLawyer = foundCase.lawyerIds?.includes(user.id);

           if (isJudge || isOfficial || isPlaintiff || isDefendant || isLawyer) {
             setCaseData(foundCase);
           } else {
             console.warn("User may not be authorized to view this mock case (client-side check).");
             toast({ variant: "destructive", title: t('toast.accessDenied.title'), description: "You are not authorized to view this case."});
             router.push('/'); // Redirect to dashboard
           }
      } else {
        console.log("No such mock case found!");
        toast({ variant: "destructive", title: "Not Found", description: t('page.caseDetail.notFound') });
        router.push('/'); // Or redirect to a dashboard/404
      }
      setLoading(false);
    };

    if (!authLoading && caseId && user) {
        fetchCase();
    } else if (!authLoading && !user) {
        toast({ variant: "destructive", title: t('toast.accessDenied.title'), description: t('accessDenied.loginRequired.page', { pageName: 'Case Details' }) });
        router.push('/login');
    }

  }, [caseId, user, authLoading, router, toast, t]);


  const handleJudgeAction = (actionKey: string) => {
    if (!caseData) return;
    const actionText = t(actionKey);
    toast({
      title: t('page.caseDetail.toast.judgeAction.title', { action: actionText }),
      description: `${t('page.caseDetail.toast.judgeAction.description', { action: actionText, caseNumber: caseData.caseNumber || '' })} (Actions performed on Judge Dashboard - Mock)`,
    });
  };


  if (authLoading || loading) {
     // Skeleton remains the same
    return (
       <div className="space-y-6 animate-pulse max-w-5xl mx-auto">
         <Skeleton className="h-10 w-24 mb-6" />
         <Card>
           <CardHeader className="border-b pb-4 bg-muted/30">
             <div className="flex flex-col sm:flex-row justify-between items-start gap-4">
               <div>
                 <Skeleton className="h-8 w-3/4 mb-2" />
                 <Skeleton className="h-4 w-1/2" />
               </div>
               <Skeleton className="h-10 w-32 rounded-md" />
             </div>
           </CardHeader>
           <CardContent className="pt-6 space-y-8">
             <div>
                <Skeleton className="h-6 w-1/4 mb-3" />
                <Skeleton className="h-4 w-full mb-1" />
                <Skeleton className="h-4 w-full mb-1" />
                <Skeleton className="h-4 w-5/6" />
             </div>
             <div>
               <Skeleton className="h-6 w-1/4 mb-3" />
               <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                 {[...Array(6)].map((_, i) => (
                   <Skeleton key={i} className="h-16 w-full rounded-md" />
                 ))}
               </div>
             </div>
             <div>
                <Skeleton className="h-6 w-1/4 mb-3" />
                <Skeleton className="h-12 w-full rounded-md mb-3" />
                <Skeleton className="h-12 w-full rounded-md" />
             </div>
             <div className="mt-8 pt-6 border-t">
               <Skeleton className="h-6 w-1/4 mb-4" />
               <Skeleton className="h-24 w-full rounded-md" />
             </div>
           </CardContent>
           <CardFooter className="bg-muted/30 p-6 flex flex-col sm:flex-row justify-between items-center gap-4">
             <Skeleton className="h-10 w-36 rounded-md" />
             <div className="flex gap-2 flex-wrap justify-center">
                <Skeleton className="h-10 w-28 rounded-md" />
                <Skeleton className="h-10 w-28 rounded-md" />
                <Skeleton className="h-10 w-36 rounded-md" />
             </div>
           </CardFooter>
         </Card>
         <p className="text-center text-muted-foreground">{t('page.caseDetail.loading')}</p>
       </div>
    );
  }

  if (!caseData) {
    return <p className="text-center py-10">{t('page.caseDetail.notFound')}</p>;
  }

  const StatusIcon = statusIcons[caseData.status] || AlertTriangleIcon;
  const statusColorClass = statusColors[caseData.status] || 'bg-gray-500/20 text-gray-700 border-gray-500/30';
  const isJudgeAssignedToCase = user?.role === 'Judge' && user.id === caseData.judgeId;

  return (
    <div className="max-w-5xl mx-auto">
      <Button variant="outline" onClick={() => router.back()} className="mb-6">
        <ArrowLeft className="mr-2 h-4 w-4" /> {t('page.caseDetail.backButton')}
      </Button>

      <Card className="shadow-xl overflow-hidden">
        <CardHeader className="border-b pb-4 bg-muted/30">
          <div className="flex flex-col sm:flex-row justify-between items-start gap-4">
            <div>
              <CardTitle className="text-3xl font-bold text-primary">{caseData.title}</CardTitle>
              <CardDescription className="text-md text-muted-foreground mt-1">
                {t('page.caseDetail.caseNumberPrefix', { caseNumber: caseData.caseNumber })}
              </CardDescription>
            </div>
            <Badge variant="outline" className={`text-lg px-4 py-2 rounded-md shrink-0 ${statusColorClass}`}>
              <StatusIcon className="mr-2 h-5 w-5" />
              {t(`status.${caseData.status}`)}
            </Badge>
          </div>
        </CardHeader>
        <CardContent className="pt-6 space-y-8">
          <Section title={t('page.caseDetail.section.overview')}>
            <p className="text-foreground leading-relaxed">{caseData.description}</p>
          </Section>

          <Section title={t('page.caseDetail.section.details')}>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-x-8 gap-y-4">
              <InfoItem icon={Users} label={t('page.caseDetail.info.plaintiff')} value={caseData.plaintiff} />
              <InfoItem icon={Users} label={t('page.caseDetail.info.defendant')} value={caseData.defendant} />
              <InfoItem icon={Landmark} label={t('page.caseDetail.info.court')} value={caseData.court} />
              {caseData.judge && <InfoItem icon={Scale} label={t('page.caseDetail.info.judge')} value={caseData.judge} />}
              {caseData.urgency && <InfoItem icon={AlertTriangleIcon} label={t('page.caseDetail.info.urgency')} value={t(`urgency.${caseData.urgency}`)} highlight={caseData.urgency === 'High'} />}
              <InfoItem icon={CalendarDays} label={t('page.caseDetail.info.filingDate')} value={formatDate(caseData.filingDate, true)} />
              <InfoItem icon={CalendarDays} label={t('page.caseDetail.info.lastUpdated')} value={formatDate(caseData.lastUpdate, true)} />
              {caseData.nextHearingDate && (
                <InfoItem icon={CalendarDays} label={t('page.caseDetail.info.nextHearingDate')} value={formatDate(caseData.nextHearingDate, true)} highlight />
              )}
            </div>
          </Section>

          {caseData.documents && caseData.documents.length > 0 && (
            <Section title={t('page.caseDetail.section.documents')}>
              <div className="space-y-3">
                {caseData.documents.map((doc, index) => (
                  <DocumentItem key={index} doc={doc} t={t} currentLang={i18n.language} />
                ))}
              </div>
            </Section>
          )}

          {caseData.judgeNotes && caseData.judgeNotes.length > 0 && (
             <Section title={t('page.caseDetail.section.judgeNotes')}>
              <div className="space-y-3">
                {caseData.judgeNotes.map((note, index) => (
                  <NoteItem key={index} note={note} t={t} currentLang={i18n.language} />
                ))}
              </div>
            </Section>
          )}

          <div className="mt-8 pt-6 border-t">
            <h3 className="text-xl font-semibold mb-4">{t('page.caseDetail.section.timeline')}</h3>
            <div className="text-center text-muted-foreground py-8">
              <CalendarDays className="mx-auto h-12 w-12 mb-2" />
              <p>{t('page.caseDetail.timelineComingSoon')}</p>
            </div>
          </div>
        </CardContent>
        <CardFooter className="bg-muted/30 p-6 flex flex-col sm:flex-row justify-between items-center gap-4">
          <Link href={`/summaries?caseId=${caseData.id}`}>
            <Button className="w-full sm:w-auto bg-accent hover:bg-accent/90 text-accent-foreground">
              <Search className="mr-2 h-4 w-4" /> {t('page.caseDetail.button.aiSummary')}
            </Button>
          </Link>
           {isJudgeAssignedToCase && (
            <div className="flex gap-2 flex-wrap justify-center">
              <span className='text-sm text-muted-foreground italic mr-2'>{t('judgeActionsPrompt')}</span>
              {/* Link to judge dashboard (even if non-functional for mock) */}
               <Link href="/judge-dashboard">
                 <Button variant="outline">
                   <LayoutDashboard className="mr-2 h-4 w-4" /> {t('goToJudgeDashboard')}
                 </Button>
              </Link>
              {/* Keep mock action buttons disabled or remove */}
              {/*
              <Button variant="outline" disabled onClick={() => handleJudgeAction('page.caseDetail.button.updateStatus')}>
                <Edit className="mr-2 h-4 w-4" /> {t('page.caseDetail.button.updateStatus')}
              </Button>
               */}
            </div>
          )}
        </CardFooter>
      </Card>
    </div>
  );
}

interface SectionProps { title: string; children: React.ReactNode; }
const Section = ({ title, children }: SectionProps) => (
  <div>
    <h3 className="text-xl font-semibold mb-3 border-b pb-2 text-primary">{title}</h3>
    {children}
  </div>
);

interface InfoItemProps {
  icon: React.ElementType;
  label: string;
  value: string | undefined | null; // Allow undefined/null values
  highlight?: boolean;
}
const InfoItem = ({ icon: Icon, label, value, highlight }: InfoItemProps) => (
  <div className={`flex items-start space-x-3 p-3 rounded-md border ${highlight ? 'bg-primary/10 border-primary/30' : 'bg-background'}`}>
    <Icon className={`h-5 w-5 mt-1 flex-shrink-0 ${highlight ? 'text-primary' : 'text-muted-foreground'}`} />
    <div>
      <p className={`text-sm font-medium ${highlight ? 'text-primary font-semibold' : 'text-foreground'}`}>{label}</p>
      <p className={`text-sm ${highlight ? 'text-primary/90' : 'text-muted-foreground'}`}>{value || t('na')}</p>
    </div>
  </div>
);


interface DocumentItemProps {
  doc: DocumentUpload;
  t: (key: string, params?: Record<string, string | number | Date>) => string;
  currentLang: string;
}
const DocumentItem = ({ doc, t, currentLang }: DocumentItemProps) => {
  const { toast } = useToast();
  const handleDownload = () => {
    toast({
      title: t('page.caseDetail.toast.downloadStarted.title'),
      description: `${t('page.caseDetail.toast.downloadStarted.description', { fileName: doc.name })} (Mock Download)`,
    });
     // Mock download: window.open(doc.url, '_blank'); // Use mock url or just show toast
  };

 // Adjusted formatting for mock data (ISO strings)
 const formatDateForDoc = (dateInput: string | Date) => {
     let date: Date | null = null;
     try {
       if (typeof dateInput === 'string') {
         date = parseISO(dateInput);
       } else if (dateInput instanceof Date) {
          date = dateInput;
       }

       if (date && isValid(date)) {
         return date.toLocaleDateString(currentLang);
       }
     } catch (e) {
       console.error("Error formatting date in DocumentItem:", e);
     }
     return t('na');
   };


  return (
    <div className="flex items-center justify-between p-3 bg-muted/50 rounded-md border hover:shadow-sm transition-shadow">
      <div className="flex items-center space-x-3 overflow-hidden mr-2">
        <FileText className="h-6 w-6 text-primary flex-shrink-0" />
        <div className="overflow-hidden">
           <p className="text-sm font-medium text-foreground truncate" title={doc.name}>{doc.name}</p>
          <p className="text-xs text-muted-foreground">
             {t('page.caseDetail.docItem.uploadedByOn', { uploader: doc.uploadedBy, date: formatDateForDoc(doc.uploadedAt)})}
            {doc.fileType && ` (${doc.fileType})`}
          </p>
        </div>
      </div>
      <Button variant="outline" size="sm" onClick={handleDownload} aria-label={t('page.caseDetail.docItem.downloadAriaLabel', { docName: doc.name })}>
        <Download className="h-4 w-4" />
      </Button>
    </div>
  );
};

interface NoteItemProps {
  note: JudgeNote;
  t: (key: string, params?: Record<string, string | number | Date>) => string;
  currentLang: string;
}
const NoteItem = ({ note, t, currentLang }: NoteItemProps) => {
    // Adjusted formatting for mock data (ISO strings)
    const formatDateForNote = (dateInput: string | Date) => {
     let date: Date | null = null;
     try {
       if (typeof dateInput === 'string') {
         date = parseISO(dateInput);
       } else if (dateInput instanceof Date) {
           date = dateInput;
        }

       if (date && isValid(date)) {
         return date.toLocaleString(currentLang, { year: 'numeric', month: 'short', day: 'numeric', hour: '2-digit', minute: '2-digit' });
       }
     } catch (e) {
       console.error("Error formatting date in NoteItem:", e);
     }
     return t('na');
   };

  return (
  <div className="p-3 bg-muted/50 rounded-md border">
    <p className="text-sm text-foreground whitespace-pre-wrap">{note.text}</p>
    <p className="text-xs text-muted-foreground mt-2">
       {t('page.caseDetail.noteItem.byOn', { author: note.author, date: formatDateForNote(note.date) })}
    </p>
  </div>
)};
